import * as yup from "yup";

export const RecoverPasswordSchema = yup.object({
  email: yup.string()
    .email("   ")
    .required(" "),
});


export const ResetPasswordSchema = yup.object({
  password: yup
    .string()
    .min(5, "    5 ")
    .matches(/[0-9a-zA-Z]/, "      ")
    .required("    "),
  confirmPassword: yup
    .string()
    .min(5, "    5 ")
    .oneOf([yup.ref("password"), null], () => "  ")
    .required("    "),
});

